button {
  cursor: pointer;
  transition: all 0.3s ease;
  outline: none;
  border: none;
}

.m-purple-btn {
  background-color: #ac5eb3;
  border: none;
}

.m-white-btn {
  background-color: #fff;
  border: none;
  border-top: 1px solid #d8d9d8;
  border-bottom: 1px solid #d8d9d8;
}

.mobile-popup {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  background-color: #fff;
  min-height: 100vh;
  max-height: calc(100vh - 60px);
  overflow: auto;
}

.m-close-popup .close-popup-btn {
  width: 20px;
  height: 20px;
  opacity: 1;
  display: block;
}
.m-close-popup .close-popup-btn:hover:before, .m-close-popup .close-popup-btn:hover:after {
  background-color: #ba78c0;
}
.m-close-popup .close-popup-btn:before, .m-close-popup .close-popup-btn:after {
  position: absolute;
  left: 15px;
  transition: all 0.3s ease;
  content: " ";
  height: 20px;
  width: 2px;
  background-color: #707070;
}
.m-close-popup .close-popup-btn:before {
  transform: rotate(45deg);
}
.m-close-popup .close-popup-btn:after {
  transform: rotate(-45deg);
}

